'use client';

interface AdBannerProps {
  size?: 'small' | 'medium' | 'large';
  className?: string;
}

export default function AdBanner({ size = 'medium', className = '' }: AdBannerProps) {
  const sizeClasses = {
    small: 'h-20',
    medium: 'h-32',
    large: 'h-48'
  };

  return (
    <div className={`bg-gray-100 border-2 border-dashed border-gray-300 rounded-lg flex items-center justify-center ${sizeClasses[size]} ${className}`}>
      <div className="text-center text-gray-500">
        <div className="text-sm font-medium mb-1">Advertisement Space</div>
        <div className="text-xs">Your ads could be here</div>
      </div>
    </div>
  );
}
